import {
  MENU_ITEM_ACTIVE,
  HEAD_ROOM,
  ITEM_QUOTE_ACTIVE,
  MODAL_CONFIRMATION_TOGGLE,
  CONFIRMATION_MODAL_PAYLOAD,
  SECTION_VIEW_TO,
  CURRENT_SECTION_PARAMS,
  PAIRS_FOR_ACCOUNT,
  OTHER_MODAL,
  CURRENT_SECTION_CLEAN,
  FLOW_ANIMATION_LAYOUT,
  FLOW_ANIMATION_OFF,
  ADD_NOTIFICATION,
  CLEAN_NOTIFICATIONS,
} from './action_types'


export const AddNotification = (payload, extra, amount) =>{
  return{
    type:ADD_NOTIFICATION,
    payload,
    amount:amount,
    extra:extra,
    meta: {
      sound: {
        play:'notification'
      }
    }
  }
}

export const CleanNotifications = (payload) =>{
  return{
    type:CLEAN_NOTIFICATIONS,
    payload
  }
}

