import React, { Fragment } from 'react'
import { SimpleLoader } from '../../../widgets/loaders'
import ItemLayout from '../../../widgets/items/itemLayout'
import bancolombia from '../../../widgets/items/assets/bank/bancolombia.png'
import ItemSelectionContainer from '../../../widgets/items/ItemSelectionContainer'
import { InputDepositForm } from '../../../widgets/inputs'
import { ButtonSuccess, ButtonForms, ButtonSuccess2 } from '../../../widgets/buttons/buttons'
import './flows.css'
import { coins, payment_method, service_modes, cash_payment } from '../../../api/ui/api.json'
import proof from '../../../../assets/proof.png'
import TicketDetail from '../../../widgets/ticket/ticketDetail'
import ModalityView from '../views/modality'
import IconSwitch from '../../../widgets/icons/iconSwitch'


// {/* ---------------------------------------------------------FLUJO TRANSFERENCIA BANCARIA.-------------------------------------------------------------------- */}


export const TransferFlow = (props) => {

  const {
    loader,
    deposit_way,
    short_bank_name,
    deposit_service,
    short_currency_name,
    finalizar,
    amount,
    step,
    actualizarEstado,
    update_control_form,
    buttonActive,
    siguiente,
    update_service_mode,
    service_mode,
    create_deposit_order,
    deposit_provider_list
  } = props



  return(
    <section className="DepositLayout" >
      {
        (step === 3 && deposit_way === "bankaccount") &&
        <div className="DLstep">
          <div className="DLcontain">
            <p className="fuente DLtitle2" >Procesaré el deposito por medio</p>
            <p className="fuente DLstitle" >de la entidad Bancaria:</p>
          </div>
          {
            !deposit_provider_list ?
            <SimpleLoader label="cargando proveedores"/>
            :
            <ItemSelectionContainer
              items={deposit_provider_list}
              type="banks"
              itemSelect={deposit_service}
              actualizarEstado={actualizarEstado}
              update_control_form={update_control_form}
            />
          }

          <ButtonForms type="primary" active={buttonActive} siguiente={siguiente}>Continuar</ButtonForms>
        </div>
      }

      {
        (step === 4 && deposit_way === "bankaccount") &&
          <ModalityView
            title="Utilizaré el servicio"
            subtitle="En la modalidad de"
            items={service_modes}
            update_service_mode={update_service_mode}
            service_mode={service_mode}
            buttonActive={buttonActive}
            deposit_service={deposit_service}
            create_deposit_order={create_deposit_order}
          />
      }

      {
        (step === 5 && deposit_way === "bankaccount") &&

        <Success
          {...props}
        />
      }
    </section>
  )
}












{/* ---------------------------------------------------------FLUJO DINERO EN EFECTIVO.-------------------------------------------------------------------- */}




export const CashFlow = (props) => {

  // console.log('LAAAAAA NUEVA PROPIEDAD DE SERVICIOOOOOOOO------------------', props)

  const {
    loader,
    deposit_way,
    short_bank_name,
    deposit_service,
    short_currency_name,
    finalizar,
    amount,
    step,
    update_local_state,
    buttonActive,
    create_deposit_order,
  } = props

  return(
    <section className="DepositLayout" >
        {
          (step === 3 && deposit_way === "cash") &&
          <ModalityView
            title="Haré el deposito por:"
            items={cash_payment}
            update_service_mode={update_local_state}
            service_mode={short_bank_name}
            buttonActive={buttonActive}
            deposit_service={deposit_service}
            create_deposit_order={create_deposit_order}
          />
        }

        {
          (step === 4 && deposit_way === "cash") &&
                <Success
                  {...props}
                />
        }
    </section>
  )
}













